import Card from "@mui/material/Card";
import CardMedia from "@mui/material/CardMedia";
import CardContent from "@mui/material/CardContent";
import Typography from "@mui/material/Typography";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import TextField from "@mui/material/TextField";
import SearchIcon from "@mui/icons-material/Search";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import img from '../../images/pexels-dziana-hasanbekava-5480827.jpg'
import React from "react";
import { getGenres } from "../../api/tmdb-api";
import { useQuery } from "react-query";
import Spinner from '../spinner'

const formControl =
{
  margin: 1,
  minWidth: 220,
  backgroundColor: "rgb(255, 255, 255)"
};

export default function FilterMoviesCard({ onUserInput, titleFilter, genreFilter, yearFilter, ratingFilter }) {

  const { data, error, isLoading, isError } = useQuery("genres", getGenres);
  

  if (isLoading) {
    return <Spinner />;
  }

  if (isError) {
    return <h1>{error.message}</h1>;
  }
  const genres = data.genres;
  if (genres[0].name !== "All") {
    genres.unshift({ id: "0", name: "All" });
  }

  // const handleChange = (e, type, value) => {
  //   e.preventDefault();
  //   props.onUserInput(type, value); // NEW
  // };

  // const handleTextChange = (e) => {
  //   handleChange(e, "name", e.target.value);
  // };

  // const handleGenreChange = (e) => {
  //   handleChange(e, "genre", e.target.value);
  // };

  // const handleYearChange = (e) => {
  //   const yearValue = e.target.value;
  //   if (yearValue >= 1980 && yearValue <= 2023) {
  //     setReleaseYear(yearValue);
  //     props.onUserInput("year", yearValue);
  //   }
  //   // handleChange(e, "year", e.target.value);
  // };

  // const handleRatingChange = (e) => {
  //   setSelectedRating(e.target.value);
  //   handleChange(e, "rating", e.target.value);
  // };



  return (
    <Card
      sx={{
        maxWidth: 345,
        backgroundColor: "rgb(204, 204, 0)"
      }}
      variant="outlined">
      <CardContent>
        <Typography variant="h5" component="h1">
          <SearchIcon fontSize="large" />
          Filter the movies.
        </Typography>
        <TextField
          sx={{ ...formControl }}
          id="filled-search"
          label="Search field"
          type="search"
          variant="filled"
          value={titleFilter}
          onChange={(e) => onUserInput("name", e.target.value)}
        />


        <TextField
          sx={{ ...formControl }}
          id="year-search"
          label="Search Year"
          type="number"
          variant="filled"
          value={yearFilter}
          onChange={(e) => onUserInput("year", e.target.value)}
          inputProps={{ min: 1980, max: 2023 }}
        />


        <FormControl sx={{ ...formControl }}>
          <InputLabel id="genre-label">Genre</InputLabel>
          <Select
            labelId="genre-label"
            id="genre-select"
            defaultValue=""
            value={genreFilter}
            onChange={(e) => onUserInput("genre", e.target.value)}
          >
            {genres.map((genre) => {
              return (
                <MenuItem key={genre.id} value={genre.id}>
                  {genre.name}
                </MenuItem>
              );
            })}
          </Select> 
        </FormControl>

        <FormControl sx={{ ...formControl }}>
          <InputLabel id="rating-select-label">Rating</InputLabel>
          <Select
            labelId="rating-select-label"
            id="rating-search"
            value={ratingFilter}
            onChange={(e) => onUserInput("rating", e.target.value)}
          >
            <MenuItem value="9+">9 and above</MenuItem>
            <MenuItem value="8-9">8 - 9</MenuItem>
            <MenuItem value="7-8">7 - 8</MenuItem>
            <MenuItem value="<7">Below 7</MenuItem>
          </Select>
        </FormControl>
      </CardContent>
      <CardMedia
        sx={{ height: 300 }}
        image={img}
        title="Filter"
      />
      <CardContent>
        <Typography variant="h5" component="h1">
          <SearchIcon fontSize="large" />
          Filter the movies.
          <br />
        </Typography>
      </CardContent>
    </Card>
  );
}